/* =================================================================== */
/* ---------------------------- Functions ---------------------------- */
/* =================================================================== */

var app = {
  tableView: {
    tableItems: [],
    selectedTableItems: [],
    page: 1,
    count: 10,
    filterType: 'Location'
  },

  /**
   * Initialize function
   */
  init: function () {
    this.initSearch();
    this.initTables();
    this.initSubmitForm();
  },

  /**
   * Initialize search form
   */
  initSearch: function () {
    this.clearSearchAll();
    this.clearSearch();
  },

  /**
   * Remove all item for search
   */
  clearSearchAll: function () {
    $('#clear_search').on('click', function () {
      $('.search__item').remove();
    });
  },

  /**
   * Remove search item
   */
  clearSearch: function () {
    $('.search__item .close-item').on('click', function () {
      $(this).closest('.search__item').remove();
    });
  },

  /**
   * Initialize the tables
   */
  initTables: function () {
    this.tableItemGenerator();
    this.pageNavigation();
    this.clearSelectedItem();
    this.changePageCount();
  },

  /**
   * Placeholder table item generator
   * Need to replace with API request
   */
  tableItemGenerator: function () {
    // Generate table items
    for (var i = 0; i < 35; i++) {
      this.tableView.tableItems.push({
        name: 'Profile ' + (i + 1),
        type: 'Location',
        idNum: (i + 1),
        created: moment().format('DD/MM/YYYY H:mm A')
      });
    }

    // Update total number of items
    $('#total').html(this.tableView.tableItems.length);

    this.updateTableItems();
  },

  /**
   * Update table items
   */
  updateTableItems: function () {
    $('.table .table__item').remove();

    // Count start and end item index
    var start = this.tableView.count * (this.tableView.page - 1);
    var end = Math.min(this.tableView.tableItems.length, this.tableView.count * this.tableView.page);

    for (var i = start; i < end; i++) {
      var index = this.tableView.selectedTableItems.findIndex(function (item) {
        return item === i;
      });
      var checked = (index > -1) ? ' checked' : '';
      var dNone = (this.tableView.selectedTableItems.length > 0) ? ' d-none' : '';

      var item = '<div class="table__item d-flex--between">\n' +
        '  <div class="flex-1">\n' +
        '    <input type="checkbox" id="item' + i + '" class="css-checkbox"' + checked + '/>\n' +
        '    <label for="item' + i + '" class="css-label"></label>\n' +
        '  </div>\n' +
        '  <div class="flex-2">' + this.tableView.tableItems[i].name + '</div>\n' +
        '  <div class="flex-2">' + this.tableView.tableItems[i].type + '</div>\n' +
        '  <div class="flex-3">' + this.tableView.tableItems[i].idNum + '</div>\n' +
        '  <div class="flex-4">' + this.tableView.tableItems[i].created + '</div>\n' +
        '  <div class="flex-4 d-flex--end">\n' +
        '    <button class="btn table__item__action view' + dNone + '">View</button>\n' +
        '    <button class="btn table__item__action edit' + dNone + '">Edit</button>\n' +
        '    <button class="btn table__item__action delete' + dNone + '" disabled>Delete</button>\n' +
        '  </div>\n' +
        '</div>';
      $('.table').append(item);
    }

    this.updateNavStatus();
    this.tableItemSelect();
  },

  /**
   * Interaction with table item
   */
  tableItemSelect: function () {
    var self = this;

    $('.table__item .css-checkbox').on('change', function () {
      var id = parseInt($(this).attr('id').substr(4));

      // Update selectedTableItems
      if (this.checked) {
        self.tableView.selectedTableItems.push(id);
      } else {
        var index = self.tableView.selectedTableItems.findIndex(function (item) {
          return item === id;
        });
        if (index > -1) {
          self.tableView.selectedTableItems.splice(index, 1);
        }
      }

      self.updateSelectedSection();
    });
  },

  /**
   * Table page navigation
   */
  pageNavigation: function () {
    var self = this;

    $('.page-nav').on('click', function () {
      if ($(this).attr('id') === 'prev') {
        if (self.tableView.page > 1) {
          // Go to previous page
          self.tableView.page -= 1;
        }
      } else {
        if (self.tableView.page < Math.ceil(self.tableView.tableItems.length / self.tableView.count)) {
          // Go to next page
          self.tableView.page += 1;
        }
      }

      self.updateTableItems();
    });
  },

  /**
   * Update page navigation status
   */
  updateNavStatus: function () {
    // Update prev/next button status
    if (this.tableView.page === 1) {
      $('#prev').addClass('disabled');
    } else {
      $('#prev').removeClass('disabled');
    }
    if (this.tableView.page === Math.ceil(this.tableView.tableItems.length / this.tableView.count)) {
      $('#next').addClass('disabled');
    } else {
      $('#next').removeClass('disabled');
    }

    // Update count of items in page
    $('#count').html(Math.min(this.tableView.tableItems.length, this.tableView.count * this.tableView.page) - this.tableView.count * (this.tableView.page - 1));
  },

  /**
   * Clear selected table items
   */
  clearSelectedItem: function () {
    var self = this;

    $('#clear_selection').on('click', function () {
      self.tableView.selectedTableItems = [];

      self.updateTableItems();
      self.updateSelectedSection();
    });
  },

  /**
   * Show/hide selected/search sections and submit/filter buttons
   */
  updateSelectedSection: function () {
    var count = this.tableView.selectedTableItems.length;
    if (count > 0) {
      $('.section__selected h2').html(count + ' Selected');
      $('.section__search').addClass('d-none');
      $('.section__selected').removeClass('d-none');
      $('.table__item__action').addClass('d-none');
      $('#submit_form').addClass('d-none');
      $('#filter_profiles').removeClass('d-none');
    } else {
      $('.section__search').removeClass('d-none');
      $('.section__selected').addClass('d-none');
      $('.table__item__action').removeClass('d-none');
      $('#submit_form').removeClass('d-none');
      $('#filter_profiles').addClass('d-none');
    }
  },

  /**
   * Change page count
   */
  changePageCount: function () {
    var self = this;

    // Show count list
    $('.page-size__current').on('click', function () {
      $(this).addClass('active');
    });

    // Update page count
    $('.page-size__list__item').on('click', function () {
      var count = parseInt($(this).html());
      $('#current').html(count);
      console.log(count);
      self.tableView.count = count;
      self.tableView.page = 1;

      self.updateTableItems();

      $('.page-size__current').removeClass('active');
    });

    // Hide count list
    document.addEventListener("mousedown", function (e) {
      var clsName = e.target.className;
      if (!clsName || typeof clsName !== 'string' || clsName.indexOf('page-size') === -1) {
        $('.page-size__current').removeClass('active');
      }
    });
  },

  /**
   * Initialize filter form
   */
  initSubmitForm: function () {
    this.toggleSubmitForm();
    this.changeTab();
    this.changeFilterType();
    this.clearFilters();
    this.updateFilter();
    this.toggleEditProfile();
  },

  /**
   * Show/hide filter form
   */
  toggleSubmitForm: function () {
    $('#submit_form').on('click', function () {
      $(this).toggleClass('active');
      $('.modal--submit').toggleClass('d-none');
    });

    $('#show_profiles').on('click', function () {
      // ToDo: add function to filter table

      $('#submit_form').removeClass('active');
      $('.modal--submit').addClass('d-none');
    });

    $('#close_filter').on('click', function () {
      $('#submit_form').removeClass('active');
      $('.modal--submit').addClass('d-none');
    });
  },

  /**
   * Change tab in submit form
   */
  changeTab: function () {
    $('.tab__list__item').on('click', function () {
      $('.tab__list__item').removeClass('active');
      $(this).addClass('active');

      $('.section__new-form').addClass('d-none');
      $('.section__prev-forms').addClass('d-none');
      $('.section__' + $(this).attr('id')).removeClass('d-none');
    });
  },

  /**
   * Change filter type
   */
  changeFilterType: function () {
    var self = this;

    $('.option__list__item').on('click', function () {
      // Update filter type button
      $('.option__list__item').removeClass('active');
      $(this).addClass('active');

      // Update filter type title
      self.tableView.filterType = $(this).html();
      $('#filter_type').html(self.tableView.filterType);

      // Show/hide filter list
      $('.filter__list--sub').addClass('d-none');
      $('#filter_' + self.tableView.filterType.toLowerCase()).removeClass('d-none');
    });
  },

  /**
   * Clear all filters
   */
  clearFilters: function () {
    $('#clear_option').on('click', function () {
      $('.filter__item').remove();
    });
  },

  /**
   * Update filters
   */
  updateFilter: function () {
    this.addFilter();
    this.removeFilter();
  },

  addFilter: function () {
    var self = this;

    $('#add_filter').on('click', function () {
      var item;
      if (self.tableView.filterType === 'Location') {
        item = '<div class="filter__item d-flex--between mb-2">\n' +
          '  <div>\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter latitude">\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter longitude">\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter radius">\n' +
          '  </div>\n' +
          '\n' +
          '  <a class="close-item">\n' +
          '    <svg class="sprite-icon" role="img" aria-hidden="true">\n' +
          '      <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="img/sprite-inline.svg#close"></use>\n' +
          '    </svg>\n' +
          '  </a>\n' +
          '</div>';
        $('#filter_location').append(item);
      } else if (self.tableView.filterType === 'Persona') {
        item = '<div class="filter__item d-flex--between mb-2">\n' +
          '  <div>\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter lat/long/radius">\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter ip address">\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter device make/model">\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter country/city">\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter language">\n' +
          '  </div>\n' +
          '\n' +
          '  <a class="close-item">\n' +
          '    <svg class="sprite-icon" role="img" aria-hidden="true">\n' +
          '      <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="img/sprite-inline.svg#close"></use>\n' +
          '    </svg>\n' +
          '  </a>\n' +
          '</div>';
        $('#filter_persona').append(item);
      } else if (self.tableView.filterType === 'Network') {
        item = '<div class="filter__item d-flex--between mb-2">\n' +
          '  <div>\n' +
          '    <input type="text" class="filter__item__input" placeholder="Enter VK ID">\n' +
          '  </div>\n' +
          '\n' +
          '  <a class="close-item">\n' +
          '    <svg class="sprite-icon" role="img" aria-hidden="true">\n' +
          '      <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="img/sprite-inline.svg#close"></use>\n' +
          '    </svg>\n' +
          '  </a>\n' +
          '</div>';
        $('#filter_network').append(item);
      }

      self.removeFilter();
    });
  },

  /**
   * Remove each filter
   */
  removeFilter: function () {
    $('.filter__item .close-item').on('click', function () {
      $(this).closest('.filter__item').remove();
    });
  },

  /**
   * Show/hide edit option
   */
  toggleEditProfile: function () {
    $('.edit-option > .btn--cancel').on('click', function () {
      $(this).toggleClass('active');
    });

    $('.edit-option__popup .btn').on('click', function () {
      $(this).closest('.edit-option').find('.btn--cancel').removeClass('active');
    });
  },
};

$(function () {
  app.init();
});